/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.tabs {
  position: fixed;
  display: flex;
  justify-content: space-around;
  bottom: 0;
  width: 100vw;
  box-shadow: 0px -4px 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
}
.tabs__item {
  padding: 8px 0 5px;
  display: inline-block;
  text-align: center;
}
.tabs__active {
  background-color: #df0a17;
  height: 24px;
  width: 24px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.tabs__img {
  display: inline-block;
  height: 22px;
  width: 22px;
}
.tabs__img--active {
  height: 18px;
  width: 18px;
}
.tabs__title {
  padding-top: 6px;
  font-size: 10px;
  line-height: 12px;
  color: #cccccc;
  font-family: PingFangSC-Regular;
}
.tabs__title--active {
  color: #df0a17;
}
.top {
  display: flex;
  justify-content: con;
}
html,
body {
  height: 100%;
  background: #f4f4f4;
  -webkit-overflow-scrolling: touch;
}
.App,
#root {
  height: 100%;
  background: #f4f4f4;
}
a {
  text-decoration: none;
  color: #000;
}
body {
  font-family: "PingFang-SC", sans-serif;
  margin: 0;
  padding: 0;
}
button {
  outline: none;
  border: none;
  padding: 0;
  background: none;
}
input,
button,
span,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
